import React, { useState, useCallback, useEffect } from 'react';
import {
  Paper,
  Button,
  Card,
  CardContent,
  Grid,
  Box,
  CircularProgress,
  Stepper,
  Step,
  StepLabel,
  Typography,
  ImageList,
  ImageListItem,
  Slider,
  Switch,
  FormControlLabel,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow
} from '@mui/material';
import { CloudUpload, Assessment } from '@mui/icons-material';
import { saveHistory } from '../utils/storage';
import { HistoryItem } from '../types/history';

const ImageRecognition: React.FC = () => {
  const [selectedImage, setSelectedImage] = useState<File | null>(null);
  const [previewUrl, setPreviewUrl] = useState<string>('');
  const [result, setResult] = useState<any>(null);
  const [loading, setLoading] = useState(false);
  const [images, setImages] = useState<File[]>([]);
  const [autoProcess, setAutoProcess] = useState(true);
  const [brightness, setBrightness] = useState(100);
  const [contrast, setContrast] = useState(100);
  const [grayscale, setGrayscale] = useState(0);
  const [imageQueue, setImageQueue] = useState<File[]>([]);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [processing, setProcessing] = useState(false);
  const [processedResults, setProcessedResults] = useState<Array<{
    file: File;
    result: any;
  }>>([]);

  const steps = ['', '', ''];

  const handleImageUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      setSelectedImage(file);
      setPreviewUrl(URL.createObjectURL(file));
      setResult(null);
    }
  };

  const handleMultipleUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const files = Array.from(event.target.files || []);
    if (files.length === 0) return;

    setImageQueue(files);
    setImages(files);
    
    // 
    setCurrentIndex(0);
    setProcessedResults([]);
    setProcessing(false);
    
    // 
    setSelectedImage(files[0]);
    setPreviewUrl(URL.createObjectURL(files[0]));
  };

  //  Blob
  const getProcessedImageBlob = useCallback(async (
    imageUrl: string,
    imageParams: {
      brightness: number;
      contrast: number;
      grayscale: number;
    }
  ): Promise<Blob> => {
    return new Promise((resolve, reject) => {
      const img = new Image();
      img.crossOrigin = 'anonymous';
      img.onload = () => {
        const canvas = document.createElement('canvas');
        canvas.width = img.width;
        canvas.height = img.height;
        const ctx = canvas.getContext('2d');
        
        if (ctx) {
          // 
          ctx.filter = `brightness(${imageParams.brightness}%) contrast(${imageParams.contrast}%) grayscale(${imageParams.grayscale}%)`;
          ctx.drawImage(img, 0, 0);
          
          canvas.toBlob((blob) => {
            if (blob) {
              resolve(blob);
            } else {
              reject(new Error('Canvas to Blob conversion failed'));
            }
          }, 'image/jpeg', 0.95);
        }
      };
      img.onerror = reject;
      img.src = imageUrl;
    });
  }, []); // 

  const processNextImage = useCallback(async () => {
    if (!imageQueue[currentIndex]) {
      setProcessing(false);
      setLoading(false);
      return;
    }

    try {
      setLoading(true);
      const currentFile = imageQueue[currentIndex];
      const currentUrl = URL.createObjectURL(currentFile);

      //  Blob
      const processedBlob = await getProcessedImageBlob(currentUrl, {
        brightness,
        contrast,
        grayscale
      });

      // API
      await new Promise(resolve => setTimeout(resolve, 2000));

      const newResult = {
        category: ``, // 
        confidence: 0.95,
        details: {
          growthStage: '',
          estimatedHarvestTime: '2',
          healthStatus: ''
        }
      };

      const historyItem: HistoryItem = {
        id: Date.now().toString(),
        date: new Date().toLocaleString(),
        imageUrl: URL.createObjectURL(processedBlob), // URL
        ...newResult
      };

      saveHistory(historyItem);
      
      setProcessedResults(prev => [...prev, { 
        file: new File([processedBlob], currentFile.name, { type: 'image/jpeg' }), 
        result: newResult 
      }]);
      setResult(newResult);
      setCurrentIndex(prev => prev + 1);
      
      // 
      const nextFile = imageQueue[currentIndex + 1];
      if (nextFile) {
        setSelectedImage(nextFile);
        setPreviewUrl(URL.createObjectURL(nextFile));
      }
      
    } catch (error) {
      console.error(':', error);
    } finally {
      setLoading(false);
    }
  }, [currentIndex, imageQueue, brightness, contrast, grayscale, getProcessedImageBlob]); //  getProcessedImageBlob 

  // 
  const handleBatchProcess = useCallback(async () => {
    if (imageQueue.length === 0 || processing) return;
    
    setProcessing(true);
    setCurrentIndex(0);
    setProcessedResults([]);
    
    // 
    const firstFile = imageQueue[0];
    setSelectedImage(firstFile);
    setPreviewUrl(URL.createObjectURL(firstFile));
    
    await processNextImage();
  }, [imageQueue, processing, processNextImage]);

  // currentIndex
  useEffect(() => {
    if (processing && currentIndex < imageQueue.length && !loading) {
      processNextImage();
    } else if (currentIndex >= imageQueue.length) {
      setProcessing(false);
    }
  }, [currentIndex, processing, imageQueue, loading, processNextImage]);

  // 
  const getImageStyle = () => ({
    filter: `
      brightness(${brightness}%) 
      contrast(${contrast}%) 
      grayscale(${grayscale}%)
    `,
    transition: 'filter 0.3s'
  });

  return (
    <Grid container spacing={3}>
      <Grid item xs={12}>
        <Paper sx={{ p: 3 }}>
          <Stepper activeStep={result ? 2 : selectedImage ? 1 : 0}>
            {steps.map((label) => (
              <Step key={label}>
                <StepLabel>{label}</StepLabel>
              </Step>
            ))}
          </Stepper>
        </Paper>
      </Grid>

      <Grid item xs={12} md={6}>
        <Card>
          <CardContent>
            <Typography variant="h6" gutterBottom>
              
            </Typography>
            <Box className="upload-zone">
              {previewUrl ? (
                <img 
                  src={previewUrl} 
                  alt="" 
                  className="preview-image"
                  style={getImageStyle()} 
                />
              ) : (
                <Box className="upload-placeholder">
                  <CloudUpload sx={{ fontSize: 60, color: 'primary.main' }} />
                  <Typography></Typography>
                </Box>
              )}
              <input
                type="file"
                accept="image/*"
                onChange={handleImageUpload}
                className="hidden-input"
              />
              <input
                type="file"
                accept="image/*"
                multiple
                onChange={handleMultipleUpload}
                className="hidden-input"
              />
            </Box>
            <Button
              variant="contained"
              onClick={handleBatchProcess}
              disabled={imageQueue.length === 0 || processing}
              fullWidth
              sx={{ mt: 2 }}
            >
              {processing ? (
                <>
                  <CircularProgress size={24} sx={{ mr: 1 }} />
                   ({currentIndex + 1}/{imageQueue.length})
                </>
              ) : (
                ''
              )}
            </Button>
          </CardContent>
        </Card>
      </Grid>

      <Grid item xs={12} md={6}>
        <Card>
          <CardContent>
            <Typography variant="h6" gutterBottom>
              
            </Typography>
            {result ? (
              <Box>
                <Typography variant="h4" color="primary" gutterBottom>
                  {result.category}
                </Typography>
                <Typography variant="subtitle1" gutterBottom>
                   {(result.confidence * 100).toFixed(2)}%
                </Typography>
                <Paper variant="outlined" sx={{ p: 2, mt: 2 }}>
                  <Typography variant="subtitle2"></Typography>
                  <Typography>{result.details.growthStage}</Typography>
                  <Typography>{result.details.estimatedHarvestTime}</Typography>
                  <Typography>{result.details.healthStatus}</Typography>
                </Paper>
              </Box>
            ) : (
              <Box className="result-placeholder">
                <Assessment sx={{ fontSize: 40, color: 'text.secondary' }} />
                <Typography color="text.secondary">
                  
                </Typography>
              </Box>
            )}
          </CardContent>
        </Card>
      </Grid>

      <Grid item xs={12}>
        <Card>
          <CardContent>
            <Typography variant="h6"></Typography>
            <FormControlLabel
              control={
                <Switch
                  checked={autoProcess}
                  onChange={(e) => setAutoProcess(e.target.checked)}
                />
              }
              label=""
            />
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <Typography></Typography>
                <Slider
                  value={brightness}
                  onChange={(_, value) => setBrightness(value as number)}
                  min={0}
                  max={200}
                  valueLabelDisplay="auto"
                />
              </Grid>
              <Grid item xs={12}>
                <Typography></Typography>
                <Slider
                  value={contrast}
                  onChange={(_, value) => setContrast(value as number)}
                  min={0}
                  max={200}
                  valueLabelDisplay="auto"
                />
              </Grid>
              <Grid item xs={12}>
                <Typography></Typography>
                <Slider
                  value={grayscale}
                  onChange={(_, value) => setGrayscale(value as number)}
                  min={0}
                  max={100}
                  valueLabelDisplay="auto"
                />
              </Grid>
              <Grid item xs={12}>
                <Button 
                  variant="outlined" 
                  onClick={() => {
                    setBrightness(100);
                    setContrast(100);
                    setGrayscale(0);
                  }}
                >
                  
                </Button>
              </Grid>
            </Grid>
          </CardContent>
        </Card>
      </Grid>
      
      {images.length > 0 && (
        <Grid item xs={12}>
          <Card>
            <CardContent>
              <Typography variant="h6"></Typography>
              <ImageList sx={{ height: 200 }} cols={4}>
                {images.map((file, index) => (
                  <ImageListItem key={index}>
                    <img
                      src={URL.createObjectURL(file)}
                      alt={` ${index + 1}`}
                      loading="lazy"
                      style={getImageStyle()}
                    />
                  </ImageListItem>
                ))}
              </ImageList>
            </CardContent>
          </Card>
        </Grid>
      )}

      {processedResults.length > 0 && (
        <Grid item xs={12}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                
              </Typography>
              <TableContainer>
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell></TableCell>
                      <TableCell></TableCell>
                      <TableCell></TableCell>
                      <TableCell></TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {processedResults.map((item, index) => (
                      <TableRow key={index}>
                        <TableCell>
                          <img
                            src={URL.createObjectURL(item.file)}
                            alt={` ${index + 1}`}
                            style={{ width: 50, height: 50, objectFit: 'cover' }}
                          />
                        </TableCell>
                        <TableCell>{item.result.category}</TableCell>
                        <TableCell>
                          {(item.result.confidence * 100).toFixed(2)}%
                        </TableCell>
                        <TableCell>{item.result.details.growthStage}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </TableContainer>
            </CardContent>
          </Card>
        </Grid>
      )}
    </Grid>
  );
};

export default ImageRecognition;
