import React, { useState, useEffect } from 'react';
import {
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
  IconButton,
  Tooltip,
  Button,
  Stack,
  Snackbar,
  Alert
} from '@mui/material';
import { Delete, Visibility, GetApp, Timeline } from '@mui/icons-material';
import { getHistory, deleteHistoryItem } from '../utils/storage';
import { HistoryItem } from '../types/history';
import HistoryDetailDialog from './HistoryDetailDialog';
import ExportDialog from './ExportDialog';
import { exportToExcel, exportToJSON } from '../utils/exportUtils';

const HistoryRecord: React.FC = () => {
  const [records, setRecords] = useState<HistoryItem[]>([]);
  const [selectedRecord, setSelectedRecord] = useState<HistoryItem | null>(null);
  const [dialogOpen, setDialogOpen] = useState(false);
  const [exportDialogOpen, setExportDialogOpen] = useState(false);
  const [snackbar, setSnackbar] = useState({
    open: false,
    message: '',
    severity: 'success' as 'success' | 'error'
  });

  useEffect(() => {
    setRecords(getHistory());
  }, []);

  const handleDelete = (id: string) => {
    const newRecords = deleteHistoryItem(id);
    setRecords(newRecords);
  };

  const handleView = (record: HistoryItem) => {
    setSelectedRecord(record);
    setDialogOpen(true);
  };

  const handleCloseDialog = () => {
    setDialogOpen(false);
    setSelectedRecord(null);
  };

  const handleExport = async (format: string, filename: string) => {
    try {
      switch (format) {
        case 'excel':
          await exportToExcel(records, filename);
          break;
        case 'json':
          exportToJSON(records, filename);
          break;
      }
      setSnackbar({
        open: true,
        message: '',
        severity: 'success'
      });
    } catch (error) {
      console.error(':', error);
      setSnackbar({
        open: true,
        message: '',
        severity: 'error'
      });
    }
  };

  return (
    <>
      <Paper sx={{ p: 3, mt: 3 }}>
        <Stack direction="row" justifyContent="space-between" mb={2}>
          <Typography variant="h5"></Typography>
          <Stack direction="row" spacing={2}>
            <Button 
              startIcon={<Timeline />}
              onClick={() => console.log('')}
            >
              
            </Button>
            <Button 
              startIcon={<GetApp />}
              variant="contained"
              onClick={() => setExportDialogOpen(true)}
            >
              
            </Button>
          </Stack>
        </Stack>
        <TableContainer>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {records.map((record) => (
                <TableRow key={record.id}>
                  <TableCell>{record.date}</TableCell>
                  <TableCell>{record.category}</TableCell>
                  <TableCell>{(record.confidence * 100).toFixed(2)}%</TableCell>
                  <TableCell>{record.details.growthStage}</TableCell>
                  <TableCell>
                    <Tooltip title="">
                      <IconButton onClick={() => handleView(record)}>
                        <Visibility />
                      </IconButton>
                    </Tooltip>
                    <Tooltip title="">
                      <IconButton onClick={() => handleDelete(record.id)}>
                        <Delete />
                      </IconButton>
                    </Tooltip>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>
      <ExportDialog
        open={exportDialogOpen}
        records={records}
        onClose={() => setExportDialogOpen(false)}
        onExport={handleExport}
      />
      <HistoryDetailDialog 
        open={dialogOpen}
        record={selectedRecord}
        onClose={handleCloseDialog}
      />
      <Snackbar
        open={snackbar.open}
        autoHideDuration={3000}
        onClose={() => setSnackbar(prev => ({ ...prev, open: false }))}
      >
        <Alert 
          severity={snackbar.severity}
          onClose={() => setSnackbar(prev => ({ ...prev, open: false }))}
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </>
  );
};

export default HistoryRecord;
